import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Modal, Button, Image } from 'react-bootstrap';
import { getCookieValue } from '../../../../utils';
import { baseUrl, FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import { HUBSPOT_CREATOR_URL } from '../../../../actions/api';

export const Video = ({ video }) => {
       return <video className='w-100 h-auto' width="186" height="330" poster={baseUrl(`media/video/${video}.png`)} controls={false} autoPlay={true} loop={true} muted={true}>
              <source src={baseUrl(`media/video/${video}.mp4`)} type="video/mp4" />
       </video>
}
class CreatorModal extends Component {
       state = { show: true, showForm: false }


       formFun = () => {
              this.setState({
                     showForm: !this.state.showForm
              })
       }

       closeModal = () => { 
              localStorage.setItem("creatorsPopup",true)
              this.setState({ show:false }) 
       }

       render() {
              const { show, showForm } = this.state;
              return (
                     <Modal id="c_c_modal" className='c_c_modal' show={show} size="lg" onHide={this.handleClose} centered>
                            {/* <Modal.Header closeButton className='border-0'> </Modal.Header> */}
                            <Modal.Body className='p-0 text-center bg-body overflow-hidden rounded-2'>
                                   <Link {...(!this.props.creatorsUser ? { to: '/home' } : {})} className='position-absolute top-0 end-0 m-4 text-gray-500 fs-5' style={{ zIndex: 1 }} onClick={this.closeModal}><FontAwesomeIcon icon="xmark" /></Link>
                                   <Image data-mode="light" className='position-absolute start-0 top-0 object-center-cover rounded-2' src={baseUrl(`media/images/gradiant-bg.svg`)} fluid />
                                   <Image data-mode="dark" className='position-absolute start-0 top-0 object-center-cover rounded-2' src={baseUrl(`media/images/gradiant-bg-dark.svg`)} fluid />
                                   <div className='position-relative'>
                                          {showForm ?
                                                 <div className='w-100 rounded-2 overflow-hidden'>
                                                        <iframe className='w-100 border-0' style={{ minHeight: 840, margin: '-30px 0px -110px 0px' }} src={HUBSPOT_CREATOR_URL}></iframe>
                                                 </div>
                                                 :
                                                 <div className='my-7'>
                                                        <h3 className='mb-3'>Looking to collaborate with UGC <span className="position-relative pb-3">Creators/Influencers?
                                                               <Image className='position-absolute start-0 bottom-0 w-100' src={baseUrl(`media/images/curve-underline.svg`)} fluid />
                                                        </span></h3>
                                                        <p className='text-gray-700'>Share your requirements with us. </p>
                                                        <Button onClick={this.formFun}>Book Discovery Call</Button>
                                                        <div className='d-flex align-items-center justify-content-center m-auto mt-4 mb-8' style={{ maxWidth: '420px' }}>
                                                               <div className="video-wrap video-wrap1 mt-8 px-3" style={{minHeight:330}}>
                                                                      <Video video={'creator-1'} />
                                                               </div>
                                                               <div className="video-wrap video-wrap2 px-3" style={{minHeight:330}}>
                                                                      <Video video={'creator-2'} />
                                                               </div>
                                                        </div>
                                                 </div>
                                          }
                                   </div>
                            </Modal.Body>
                     </Modal>
              );
       }
}

export default CreatorModal;