import React, { Component, Suspense } from 'react';
import CreatorModal from './CreatorModal';
import { Image } from 'react-bootstrap';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { UGC_EMBED_URL_JSON, UGC_EMBED_REDIRECT_URL } from '../../../../actions/api';
import { isCreatorsUser } from '../../../../utils';


export const ADD_SCRIPT = url => {
    const script = document.head.appendChild(document.createElement("script"))
    script.src = url
    script.async = true
    document.body.appendChild(script)
}

class Creators extends Component {
    state = { isembedCall: false }
    componentDidMount() {
        const { userDetail } = this.props
        if (isCreatorsUser() && localStorage.getItem("token")) {
            localStorage.setItem("access_token", localStorage.getItem("token"))
            this.onLoad()
        }
    }
    onLoad = () => {
        this.setState({ isembedCall: false })
        var AppPath = `${UGC_EMBED_URL_JSON}`;
        fetch(AppPath)
            .then(response => response.json())
            .then(json => {
                const { css, id, script } = json;
                let myScript = document.createElement("div");
                myScript.setAttribute("id", id);
                document.getElementById('influencer').appendChild(myScript);

                let myCss = document.createElement("link");
                myCss.setAttribute("type", 'text/css');
                myCss.setAttribute("id", 'influencer_css');
                myCss.setAttribute("rel", 'stylesheet');
                myCss.setAttribute("href", css);
                //    document.head.appendChild(myCss);
                document.head.insertBefore(myCss, document.head.firstChild);

                let myreact = document.createElement("script");
                myreact.setAttribute("type", 'text/javascript');
                myreact.setAttribute("id", 'influencer_js');
                myreact.setAttribute("src", script);
                document.body.appendChild(myreact);

            }).catch(err => {
                this.setState({ isembedCall: false })

            })

    }
    componentWillReceiveProps(nextProps) {
        if (localStorage.getItem("token") && nextProps && isCreatorsUser()) {
            localStorage.setItem("access_token", localStorage.getItem("token"))
            if (this.state.isembedCall) this.onLoad()
        }
    }

    componentWillUnmount() {
        const { userDetail } = this.props
        if (isCreatorsUser()) {
            this.onLoadRemoveScript()
            this.onLoadRemoveCss()
        }
    }
    onLoadRemoveCss = () => {
        try {
            const scriptList = document.querySelectorAll("link[type='text/css']")
            const convertedNodeList = Array.from(scriptList)
            const testScript = convertedNodeList.find(script => script.id === "influencer_css")
            testScript.parentNode.removeChild(testScript)
        }
        catch (ex) {

        }
    }
    onLoadRemoveScript = () => {
        try {
            const scriptList = document.querySelectorAll("script[type='text/javascript']")
            const convertedNodeList = Array.from(scriptList)
            const testScript = convertedNodeList.find(script => script.id === "influencer_js")
            testScript.parentNode.removeChild(testScript)
        }
        catch (ex) {

        }
    }



    render() {
        const { userDetail } = this.props
        return <>
            { isCreatorsUser() ? 
                <div id="influencer" data-color="" data-url={UGC_EMBED_REDIRECT_URL}> 
                {!localStorage.getItem('creatorsPopup') ? <CreatorModal creatorsUser={true}/> :''}
                </div> 
                : <div className='position-relative'>
                    <Image data-mode="light" className='opacity-75' src={baseUrl('media/images/creator-blur.png')} height={46} width={150} alt={'creator'} style={{ margin: '0 -15px', width: 'calc(100% + 30px)' }} fluid />
                    <Image data-mode="dark" className='opacity-75' src={baseUrl('media/images/creator-blur-dark.png')} height={46} width={150} alt={'creator'} style={{ margin: '10 -15px', width: 'calc(100% + 30px)' }} fluid />
                    <CreatorModal />
                </div>
            }
        </>





    }
}

export default Creators